import { useEffect, useRef } from 'react';
import { BrowserMultiFormatReader } from '@zxing/browser';

export default function BarcodeScanner({ onDetected }) {
  const videoRef = useRef(null);
  const codeReaderRef = useRef(null);

  useEffect(() => {
    const codeReader = new BrowserMultiFormatReader();
    codeReaderRef.current = codeReader;

    const startScanner = async () => {
      try {
        await codeReader.decodeFromVideoDevice(null, videoRef.current, (result, err) => {
          if (result) {
            const isbn = result.getText().replace(/[^0-9X]/gi, '');
            if (isbn.startsWith('978') || isbn.startsWith('979')) {
              onDetected(isbn);
              try {
                if (codeReader.reset) {
                  codeReader.reset();
                } else if (codeReader.resetDecoder) {
                  codeReader.resetDecoder();
                }
              } catch (resetError) {
                console.warn("Reset failed:", resetError);
              }
            }
          }
        });
      } catch (error) {
        console.error('Camera error:', error);
      }
    };

    startScanner();

    return () => {
      try {
        if (codeReaderRef.current?.reset) {
          codeReaderRef.current.reset();
        } else if (codeReaderRef.current?.resetDecoder) {
          codeReaderRef.current.resetDecoder();
        }
      } catch (err) {
        console.warn("Error during cleanup:", err);
      }
    };
  }, [onDetected]);

  return (
    <div>
      <video ref={videoRef} width="100%" style={{ borderRadius: "10px" }} autoPlay muted />
      <p>📷 Point your camera at an ISBN barcode</p>
    </div>
  );
}
